@tailwind base;
@tailwind components;
@tailwind utilities;

@layer utilities {
    .my-header {
        @apply w-full h-screen bg-gradient-to-b from-black via-black to-slate-300
    }
    .my-title {
        @apply text-black text-4xl font-bold mb-4
    }
    .my-subtitle {
        @apply text-gray-600 text-xl
    }
}